<template>
  <div class="todo-footer" v-show="total">

    <label>
      <input v-model="isAll" type="checkbox"/>
    </label>

    <span>
      <span>已完成{{donetTotal}}</span> / 全部{{todos.length}}
    </span>

    <button @click="clearAll" class="btn btn-danger">清除已完成任务</button>
  </div>
</template>

<script>
export default {
  name:'Footer',
  props:['todos','checkAllTodo','clearAllTodo'],
  computed:{
    total(){
      return this.todos.length
    },
    donetTotal(){
      // 比较low
      /* let i =0;
      this.todos.forEach((todo) => {
        if(todo.done)i++
      })
      return i */
      return this.todos.reduce((pre,current) => {return pre+current.done},0)
    },
    isAll:{
      get(){
        return this.donetTotal === this.total && this.total !== 0
      },
      set(value){
        this.checkAllTodo(value)
      },
    },
  },
  methods:{
    /* checkAll(e){
      this.checkAllTodo(e)
    }, */
    clearAll(){
      this.clearAllTodo()
    }
  }
}
</script>

<style scoped>
  .todo-footer {
    height: 40px;
    line-height: 40px;
    padding-left: 6px;
    margin-top: 5px;
  }

  .todo-footer label {
    display: inline-block;
    margin-right: 20px;
    cursor: pointer;
  }

  .todo-footer label input {
    position: relative;
    top: -1px;
    vertical-align: middle;
    margin-right: 5px;
  }

  .todo-footer button {
    float: right;
    margin-top: 5px;
  }
</style>