<template>
  <div>

    <div class="todo-container">
      <div class="todo-wrap">
        <Header :addTodo="addTodo"/>
        <List 
          :todos="todos" 
        />
        <Footer :clearAllTodo="clearAllTodo" :todos="todos" :checkAllTodo="checkAllTodo"/>
      </div>
    </div>
    
  </div>
</template>

<script>
  import pubsub from 'pubsub-js'
  import Header from './components/Header.vue'
  import List from './components/List.vue'
  import Footer from './components/Footer.vue'

  export default {
    name:'App',
    components:{
      Header,
      List,
      Footer,
      Header
    },
    methods:{
      // 添加一个todo
      addTodo(todoObj){
        this.todos.unshift(todoObj)
      },
      // 勾选或者取消勾选一个todo
      checkTodo(id){
        this.todos.forEach((todoObj) => {
          todoObj.done=(todoObj.id === id?!todoObj.done:todoObj.done)
        })
      },
      // 更新一个todo
      updateTodo(id,title){
        this.todos.forEach((todo) => {
          if(todo.id === id) todo.title =title
        })
      },
      // 删除一个todo
      deleteTodo(_,id){
        this.todos = this.todos.filter(todo => id !== todo.id)
      },
      // 全选or取消全选
      checkAllTodo(done){
        this.todos.forEach((todo) => {
          todo.done = done
        })
      },
      // 清除所有已完成的todo
      clearAllTodo(){
        this.todos = this.todos.filter((todo) => {
          return !todo.done
        })
      }
    },
    data() {
      return {
        msg:'欢迎梁哥🙋🏻',
        todos:JSON.parse(localStorage.getItem('todos')) || []
      }
    },
    // 保存todos
    watch:{
      todos:{
        deep:true,
        handler(value){
          localStorage.setItem('todos',JSON.stringify(value))
        }
      }
    },
    mounted(){
      this.$bus.$on('checkTodo',this.checkTodo)
      this.pubId = pubsub.subscribe('deleteTodo',this.deleteTodo)
      this.$bus.$on('updataTodo',this.updateTodo)
    },
    beforeDestroy() {
      this.$bus.$off('checkTodo')
      this.$bus.$off('updateTodo')
      pubsub.unsubscribe(this.pubId)
    },
  }
</script>

<style>

  body {
    background: #fff;
  }

  .btn {
    display: inline-block;
    padding: 4px 12px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }

  .btn-danger {
    color: #fff;
    background-color: #da4f49;
    border: 1px solid #bd362f;
  }

  .btn-edit {
    color: #fff;
    background-color:skyblue;
    border: 1px solid rgb(52, 93, 109);
    margin-right: 5px;
  }

  .btn-danger:hover {
    color: rgb(111, 196, 230);
    background-color: #bd362f;
  }

  .btn:focus {
    outline: none;
  }

  .todo-container {
    width: 600px;
    margin: 0 auto;
  }
  .todo-container .todo-wrap {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
</style>