// 引入Vue
import Vue from "vue";
// 引入App
import App from './App.vue'
// 关闭Vue的生成提示
Vue.config.productionTip = false

// 注册一个全局自定义指令 `v-focus`
Vue.directive('focus', {
  // 当被绑定的元素插入到 DOM 中时……
  update: function (el,{value}) {
    if(value){
      // 聚焦元素
      el.focus()
    }
  }
})
// 创建vm
new Vue({
  el:'#app',
  render: h => h(App),
  beforeCreate(){
    Vue.prototype.$bus = this
  }
})