<template>
  <!-- <transition name="todo" appear=""> -->
    <li>
    <label>
      <input type="checkbox" :checked="todo.done" @change="handleCheck(todo.id)"/>
      <!-- 不推荐直接修这种做法，因为修改了props -->
      <!-- <input type="checkbox" v-model="todo.done"/> -->
      <span v-show="!todo.isEdit">{{todo.title}}</span>
      <input 
        type="text" 
        @blur="handleBlur(todo,$event)" 
        v-show="todo.isEdit" 
        :value="todo.title"
        @keyup.enter="handleBlur(todo,$event)"
        v-focus="focusStatus"
      >
    </label>
    <button @click="handleDelete(todo.id)" class="btn btn-danger">删除</button>
    <button v-show="!focusStatus" @click="handleEdit(todo,$event)" class="btn btn-edit">编辑</button>
  </li>
  <!-- </transition> -->
</template>

<script>
import PubSub from 'pubsub-js'

export default {
  name:'Item',
  data(){
    return {
      focusStatus:false,
    }
  },
  // 声明接收todo对象 行参 名字跟List属性名一样
  props:['todo',],
  methods:{
    // 勾选/取消勾选
    handleCheck(id){
      // 通知App组件将对应的todo对象的done取反
      this.$bus.$emit('checkTodo',id)
    },
    // 删除
    handleDelete(id){
      if(confirm('确认删除吗👩🏻️')){
        // 通知App删除对应的todo对象
        // this.$bus.$emit('deleteTodo',id)
        PubSub.publish('deleteTodo',id)
      }
    },
    // 编辑
    handleEdit(todo,$event){
      if(todo.hasOwnProperty('isEdit')){
        todo.isEdit = true
        console.log('已经有了');
      }else{
        this.$set(todo,'isEdit',true)
        console.log('没有');
      }
      // todo.isEdit = true
      this.focusStatus = !this.focusStatus
      // 如果要手动获取焦点，需要在DOM更新后及input以及显示后才能获取焦点，用nextTick在下一次DOM更新后执行回调
      /* this.$nextTick(function(){
        this.$refs.inputTitle.focus()
      }) */
    },
    // Enter&失去焦点回调 执行修改逻辑
    handleBlur(todo,$event){
      this.todo.isEdit = this.focusStatus = false
      console.log('updataTodo',todo.id,$event.target.value);
      if(!$event.target.value.trim()) return alert('输入不能为空！')
      this.$bus.$emit('updataTodo',todo.id,$event.target.value)
    }
  }
}
</script>

<style scoped lang="less">
  li {
    list-style: none;
    height: 36px;
    line-height: 36px;
    padding: 0 5px;
    border-bottom: 1px solid #ddd;
  }

  li label {
    float: left;
    cursor: pointer;
  }

  li label li input {
    vertical-align: middle;
    margin-right: 6px;
    position: relative;
    top: -1px;
  }

  li button {
    float: right;
    display: none;
    margin-top: 3px;
  }

  li:before {
    content: initial;
  }

  li:last-child {
    border-bottom: none;
  }

  li:hover {
    background-color: #ddd;
    button {
      display: block;
    }
  }

  /* .todo-enter-active {
    animation: todo 0.5s;
  }
  .todo-leave-active {
    animation: todo 0.5s linear reverse;
  }

  @keyframes todo {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(0px);
    }
  } */
</style>