<template>
  <ul class="todo-main" v-show="todos.length!==0">
    <transition-group name="todo" appear>
      <Item 
      :todo="todoObj" 
      v-for="(todoObj) in todos" 
      :key="todoObj.id"
    />
    </transition-group>
  </ul>
</template>

<script>
import Item from './Item.vue'
// import demo from '../demo'
export default {
  name:'List',
  components:{Item},
  props:['todos',]
}
</script>

<style scoped>
  .todo-main {
    margin-left: 0px;
    border: 1px solid #ddd;
    border-radius: 2px;
    padding: 0px;
  }

  .todo-empty {
    height: 40px;
    line-height: 40px;
    border: 1px solid #ddd;
    border-radius: 2px;
    padding-left: 5px;
    margin-top: 10px;
  }
  
  .todo-enter-active {
    animation: todo 0.5s;
  }
  .todo-leave-active {
    animation: todo 0.5s linear reverse;
  }

  @keyframes todo {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(0px);
    }
  }
</style>