<template>
  <div class="todo-header">
    <input @keyup.enter="add" type="text" placeholder="请输入你的任务名称，按回车键确认"/>
  </div>
</template>

<script>
import {nanoid} from 'nanoid'
// import demo from '../demo'

export default {
  name:'Header',
  methods:{
    add(e){
      // 校验数据
      if(!e.target.value.trim()) return (e.target.value = '',alert('输入不能为空'))
      // 将用户的输入包装成为一个todo对象
      const todoObj = {
        id:nanoid(),
        title:e.target.value.trim(),
        done:false
      }
      this.todoObj = todoObj
      // demo.todo = {...todoObj};
      // console.log(todoObj);
      // 通知App组件去添加一个todo对象
      this.addTodo(todoObj)
      // 清空输入
      e.target.value = ''
    }
  },
  data(){
    return {
      todoObj:{},
    }
  },
  props:['addTodo']
}
</script>

<style scoped>
   .todo-header input {
    width: 560px;
    height: 28px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 4px 7px;
  }

  .todo-header input:focus {
    outline: none;
    border-color: rgba(82, 168, 236, 0.8);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
  }
</style>